.adalabers-card {
  margin-bottom: 45px;
  font-family: "Open Sans";
  font-size: 1rem; /* 16px */
}

/* container for a responsive img */
.card-image-container {
  margin: 0 auto;
  max-width: 180px;
  max-height: 180px;
  overflow: hidden; /* review */
}

.card-image {
  display: block;
  width: 100%;
  border: solid 5px rgba(0, 0, 0, 0.08);
  border-radius: 4px;
}

.card-info-container {
  width: 100%;
  margin: 0 auto;
}

.card-name {
  margin: 15px 0;
  font-size: 1.25rem;
  line-height: 2.25;
}

.card-text {
  color: #54585a;
  line-height: 1.5;
  margin-bottom: 15px;
}

.card-list {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  width: 120px;
}

.card-link {
  color: black;
  text-decoration: none;
}

/* icons style and cosmetics*/

.fab,
.fas {
  color: #099d8d;
  font-size: 1rem; /* 16px */
}

.fab::after {
  content: "·";
  color: #54585a;
  padding: 5px;
}
@media all and (min-width: 768px) {
  .adalabers-card {
    margin-bottom: 60px;
  }

  .card-wrapper {
    display: flex;
    align-items: center;
  }

  .card-reverse {
    flex-direction: row-reverse;
  }

  .card-image-container {
    margin: 0 auto;
    max-width: 205px;
    max-height: 205px;
  }

  .card-info-container {
    text-align: left;
    margin-left: 30px;
    width: 70%;
  }

  .info-reverse {
    margin-right: 30px;
    margin-left: 0;
  }

  .card-list {
    margin: 0;
  }
}

@media all and (min-width: 1200px) {
  /* ADALABERS media */
  .adalabers-card {
    max-width: 50rem;
    margin: 60px auto;
  }

  .card-wrapper {
    justify-content: center;
  }

  .card-image-container {
    margin: 0 auto;
    max-width: 278px;
    max-height: 278px;
  }

  .card-info-container {
    text-align: left;
    margin-left: 45px;
    width: 27rem;
  }
}
